import React, { useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import { Grid, Button } from "@material-ui/core";
import UnfoldMoreIcon from "@material-ui/icons/UnfoldMore";

const useStyles = makeStyles((theme) => ({
  start: {
    border: "1px solid red",
  },
  inputDate: {
    marginRight: 5,
    width: 155,
    height: 30,
    border: "1px solid #5e6bf7",
  },
  textNews: {
    marginBottom: 5,
    listStyle: "none",
  },
}));

export default function Search(props) {
  const classes = useStyles();

  var [state, setState] = useState("");

  var libraries = Object.values(props.object),
    searchString = state.trim().toLowerCase();
  if (searchString.length > 0) {
    libraries = libraries.filter(function (i) {
      return i.toLowerCase().match(searchString);
    });
  }

  const handleChange = (e) => {
    setState(e.target.value);
  };

  //style={{ border: "1px solid red" }
  return (
    <div>
      <Grid
        container
        direction="column"
        justify="center"
        alignItems="center"
        spacing={1}
      >
        <Grid item>
          <Grid container direction="row">
            {
              // <Grid item>
              //   <input
              //     className={classes.inputDate}
              //     type="text"
              //     onChange={handleChangeDate1}
              //     placeholder="Data inicial (dd/mm/aaaa)"
              //   />
              // </Grid>
              // <Grid item>
              //   <input
              //     type="text"
              //     className={classes.inputDate}
              //     onChange={handleChangeDate2}
              //     placeholder="Data final (dd/mm/aaaa)"
              //   />
              // </Grid>
            }
            <Grid item>
              <input
                type="text"
                value={state}
                style={{
                  width: 300,
                  height: 30,
                  border: "1px solid #5e6bf7",
                }}
                onChange={handleChange}
                placeholder="Pesquise a notícia"
              />
            </Grid>
            <Grid item>
              {
                // <Button onClick={() => changeOrder()}>
                //   <UnfoldMoreIcon />
                // </Button>
              }
            </Grid>
          </Grid>
        </Grid>
        <Grid item>
          <ul>
            {libraries.map(function (i) {
              return <li className={classes.textNews}>{i}</li>;
            })}
          </ul>
        </Grid>
      </Grid>
    </div>
  );
}
