import React, { useRef, useEffect } from "react";
import DenseAppBar from "../../components/structure/appbar/AppBar";
import { Footer } from "../../components/structure/footer";
import { Grid, Typography, Box, List, ListItem, Link } from '@material-ui/core'
import { makeStyles } from "@material-ui/core/styles";
import  data  from './data'

const useStyles = makeStyles((theme) => ({
  page: {
    display: "flex",
    flexDirection: "column",
    backgroundColor: "#f3f4f5",
    marginTop: "65px", 
    overflowX: "hidden",
    overflowY: "hidden",
    minHeight: "93vh",
  },

  title:  {
    marginLeft: '10px', 
    color: "#343894",
  }, 

  rootDataFlexibilization: {
    marginTop: "2%", 
    marginLeft:'20%',  
    marginRight: '20%',  
  } 

}));

const redirectToBigCard = (ref) => {
  if (ref.current === null) {
    // treatment solution
    window.scrollTo(0, 0);
  } else {
    window.scrollTo(0, ref.current.offsetTop);
  }
};

function DataFlexibilization() {

  const classes = useStyles(); 

  return (
    <div className={classes.rootDataFlexibilization}>

      <Box borderLeft={10} borderColor="#1090d8" className={classes.box}> 
        <Typography variant='h4' className={classes.title} > {data.title} </Typography> 
        <Typography variant='subtitle' className={classes.title} > {data.subtitle} </Typography>
      </Box>

      <Grid 
        container
        direction="column"
        justify="flex-start"
        alignItems="stretch"
        spacing={3}
      >

        <Grid item />
        

        <Grid item >
          <Typography variant='h6' align='center' > 
            <Box fontWeight="fontWeightBold">
              {data.planoDeflexibilizacao.title} 
            </Box>
          </Typography>
        </Grid>

        <Grid item >
          <Typography variant='body' align='justify'> {data.planoDeflexibilizacao.p1} </Typography>
        </Grid>
        
        <Grid item >
          <List>
            { data.planoDeflexibilizacao.topics.map((topic) => (
                <ListItem>
                  <Typography variant='body' align='justify' > {"- " + topic} </Typography>
                </ListItem> 
              ))}
          </List>
        </Grid>

        <Grid item >
          <Typography variant='body' align='justify'> {data.planoDeflexibilizacao.p2} </Typography>
        </Grid>
        

        <Grid item >
          <Typography variant='body' > {data.planoDeflexibilizacao.p3} </Typography>
          <Link href={data.planoDeflexibilizacao.linkNotaTecnica} >
            Acesse a nota técnica aqui
          </Link>
        </Grid>

        <Grid item >
          <Typography variant='body' align='justify'> {data.planoDeflexibilizacao.p4} </Typography>
          <Link href={data.planoDeflexibilizacao.linkGuia} >
            Acesse o guia aqui
          </Link>
        </Grid>
        

        <Grid item >
          <Typography variant='h6' align='justify' > 
              <Box fontWeight="fontWeightBold">
                {data.criterios.title} 
              </Box>
          </Typography>
        </Grid>
        
        <Grid item >
          <Typography variant='body' align='justify'> {data.criterios.p1} </Typography>
        </Grid>

        <Grid item >
          <Typography variant='body' align='justify'> TABELA </Typography>
        </Grid>

        <Grid item >
            <Typography variant='body' align='justify'> {data.criterios.p2} </Typography>
        </Grid>
        



        
        
        <Grid item />
      </Grid>
    </div>
  ); 
}


export function FlexibilizationPage(props) {
  const classes = useStyles();

  //references
  const start = useRef(null);

  const situacao = useRef(null);
  const mapas = useRef(null);
  const plano = useRef(null);
  const criterios = useRef(null);

  // receive value of link of state
  var { foo } = props.location.state || "start";

  // treatment solution
  if (foo === "start" || foo === undefined || foo === null) {
    redirectToBigCard(start);
  }

  useEffect(() => {
    if (foo === "1") {
      redirectToBigCard(situacao);
    } else if (foo === "2") {
      redirectToBigCard(mapas);
    } else if (foo === "3") {
      redirectToBigCard(plano);
    } else if (foo === "4") {
      redirectToBigCard(criterios);
    }
  }, []);

  return (
    <div className={classes.page}>
      <DenseAppBar ref={start} />
      <DataFlexibilization />
      <Footer />
    </div>
  );
}
