import React, { useState, useEffect } from "react";
import DenseAppBar from "../../components/structure/appbar/AppBar";
import { Footer } from "../../components/structure/footer";
import { makeStyles } from "@material-ui/core/styles";

import data from "./datasNews";
import { Grid, Typography, Link, Box } from "@material-ui/core";
import { Link as LinkRouter } from "react-router-dom";

import Firebase from "../../db/firebase";
import { fontSize } from "@material-ui/system";

import Search from "./search.js";

const listSearch = ["teste", "lucas", "legal"];

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
    flexDirection: "column",
    backgroundColor: "#f3f4f5",
    marginTop: 35,
    overflowX: "hidden",
    overflowY: "hidden",
    minHeight: "93vh",
  },
  title: { marginTop: 50 },
  text: {
    marginLeft: 20,
    marginRight: 20,
  },
  imageBrazil: {
    width: theme.spacing(35),
    height: theme.spacing(40),
  },
  imageParaiba: { width: theme.spacing(40), height: theme.spacing(30) },
}));

var db = Firebase.firestore();

function Space(props) {
  const style = makeStyles((theme) => ({
    space: {
      marginLeft: props.left,
      marginRight: props.right,
      marginTop: props.top,
      marginBottom: props.bottom,
    },
  }));
  const classe = style();

  return <div className={classe.space}></div>;
}

function SecondaryTitle(props) {
  const style = makeStyles((theme) => ({
    style: {
      color: "#5e6bf7",
      fontSize: props.sizeFont,
    },
  }));
  const classe = style();

  return (
    <div>
      <Typography className={classe.style} variant="h4" color="#1a237e">
        {props.text}
      </Typography>
    </div>
  );
}

function getFirebase(collection, document, setState) {
  var docDB = db.collection(collection).doc(document);
  docDB
    .get()
    .then(function (doc) {
      if (doc.exists) {
        setState(doc.data());
      } else {
        setState(["Ocorreu um erro, tente novamente."]);
      }
    })
    .catch(function (error) {
      setState(["Ocorreu um erro, tente novamente."]);
    });
}

export function News(props) {
  const classes = useStyles();

  const [news, setNews] = useState({});

  useEffect(() => {
    getFirebase("news", "news", setNews);
  }, []);

  return (
    <div className={classes.root}>
      <DenseAppBar />
      <Grid
        container
        direction="column"
        alignItems="center"
        justify="center"
        spacing={1}
      >
        <Grid item>
          <Typography className={classes.title} variant="h4" color="#1a237e">
            Notícias
          </Typography>
        </Grid>

        <Space top={25} />

        {data.news.map((paragraph) => (
          <Grid className={classes.text} item xs={11.5}>
            <Typography align="justify"> {paragraph} </Typography>
          </Grid>
        ))}

        <Space top={25} />

        <Search object={news} />

        <Space top={30} />

        <Grid className={classes.text} item xs={11.5}>
          <Typography>
            {" "}
            <b>{data.disclaimer[0].slice(0, 10)} </b>{" "}
          </Typography>
        </Grid>
        <Space top={10} />

        {data.disclaimer.map((paragraph) => (
          <Grid className={classes.text} item xs={11.5}>
            <Typography align="justify"> {paragraph.slice(11)} </Typography>
          </Grid>
        ))}

        <Space bottom={30} />
      </Grid>

      <Space bottom={30} />
      <Footer />
    </div>
  );
}
